import React, { useState } from "react";
import Box from "@mui/material/Box";
import CardContent from "@mui/material/CardContent";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import SchoolIcon from "@mui/icons-material/School";
import Avatar from "@mui/material/Avatar";
import { get, upperCase } from "lodash";
import { Card, Chip, Divider, Link, Stack } from "@mui/material";
import moment from "moment";
import translate from "../../hooks/translate";
import TwitterIcon from "@mui/icons-material/Twitter";
import FacebookIcon from "@mui/icons-material/Facebook";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import InstagramIcon from "@mui/icons-material/Instagram";
import YouTubeIcon from "@mui/icons-material/YouTube";
import BusinessCenterOutlinedIcon from "@mui/icons-material/BusinessCenterOutlined";
import CardMembershipIcon from "@mui/icons-material/CardMembership";
import MedicalInformationIcon from "@mui/icons-material/MedicalInformation";
import CopyToClipboard from "react-copy-to-clipboard";
import CMToolTip from "../../ui-components/tooltip/CMToolTip";
import LocationOnIcon from "@mui/icons-material/LocationOn";

const MobileViewProfile = ({ consultantMobileUIData }) => {
  console.log("consultantMobileUIData :>> ", consultantMobileUIData);
  const [isCopied, setIsCopied] = useState(false);
  const getIconComponent = (socialMediaChanel) => {
    switch (socialMediaChanel) {
      case "Twitter":
        return <TwitterIcon />;
      case "Facebook":
        return <FacebookIcon />;
      case "LinkedIn":
        return <LinkedInIcon />;
      case "Instagram":
        return <InstagramIcon />;
      case "YouTube":
        return <YouTubeIcon />;
      default:
        return <BusinessCenterOutlinedIcon />;
    }
  };

  return (
    <div className="iphone-x">
      {/* <i>Speaker</i>
      <b>Camera</b> */}
      <Box m={1} sx={{ "& card": { mt: 2 } }}>
        <Grid container mt={2}>
          {/* ================Profile=============== */}
          <Grid item xs={4}>
            <Avatar
              // variant="rounded"
              // sx={{ width: 56, height: 56 }}
              sx={{
                height: { xs: "150px", md: "80px" },
                width: { xs: "150px", md: "80px" },
                backgroundColor: "primary.lighter",
                margin: { xs: "auto", md: "0" },
              }}
              alt={upperCase(get(consultantMobileUIData, "firstName", "U"))}
              src={get(consultantMobileUIData, "profilePictureURL", "NA")}
            />
          </Grid>
          {/* ================Profile Detail=============== */}
          <Grid item xs={8} style={{ alignSelf: "center" }}>
            <Stack direction="column">
              <Typography variant="body2" className="fw-bold">
                {`${consultantMobileUIData?.firstName} ${consultantMobileUIData?.lastName}`}
              </Typography>
              {/* <Typography variant="overline">
                âŸŸ {`${consultantMobileUIData?.country?.name}`}
              </Typography> */}
            </Stack>{" "}
            <Stack direction="row" spacing={1}>
              {get(consultantMobileUIData, "specialization", []).map(
                (item, index) =>
                  index < 2 && (
                    <Chip
                      key={index}
                      label={upperCase(
                        translate(get(item, "foreignData.macroName", ""))
                      )}
                      size="small"
                    />
                  )
              )}
            </Stack>
            <Stack direction="row">
              {get(consultantMobileUIData, "socialMediaInfo", []).map(
                (item, index) => (
                  <Box component="span">
                    {/* {" "}
                    <Avatar
                      sx={{
                        backgroundColor: "grey.300",
                        color: "text.secondary",
                      }}
                    >
                    
                    </Avatar> */}
                    <CMToolTip
                      icon={
                        <CopyToClipboard
                          text={item.chanelLink}
                          onCopy={() => setIsCopied(true)}
                        >
                          {getIconComponent(item.socialMediaChanel)}
                        </CopyToClipboard>
                      }
                      title="Copied!!!"
                    />
                  </Box>
                )
              )}
            </Stack>
          </Grid>
          {/* ================BIO=============== */}
          <Grid item xs={12} mt={2}>
            <Card variant="outlined">
              <CardContent>
                {/* <Typography sx={{ fontSize: 14 }} color="text.secondary" mb={1}>
                  BIO
                </Typography> */}
                <Typography variant="body2">
                  {consultantMobileUIData?.bio}
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          {/* ================LANGUAGE SPOKEN=============== */}
          {/* <Grid item xs={12} mt={2}>
            <Card variant="outlined">
              <CardContent>
                <Typography sx={{ fontSize: 14 }} color="text.secondary" mb={1}>
                  LANGUAGE SPOKEN
                </Typography>
                <Stack direction="row" spacing={1}>
                  <Chip label="Small" size="small" />
                  <Chip label="Small" size="small" />
                  <Chip label="Small" size="small" />
                </Stack>
              </CardContent>
            </Card>
          </Grid> */}

          {/* ================EDUCATION=============== */}
          <Grid item xs={12} mt={2}>
            <Card variant="outlined">
              <CardContent>
                {/* <Typography sx={{ fontSize: 14 }} color="text.secondary" mb={1}>
                  EDUCATION
                </Typography> */}
                {get(consultantMobileUIData, "educationUniversity", []).map(
                  (item, index, { length }) => (
                    <Box key={index}>
                      <Stack direction="row" align="center">
                        <Box component="span">
                          <Avatar
                            sx={{
                              backgroundColor: "grey.300",
                              color: "text.secondary",
                            }}
                          >
                            <SchoolIcon />
                          </Avatar>
                        </Box>
                        <Box sx={{ textAlign: "start", marginLeft: "10px" }}>
                          <Typography
                            variant="subtitle2"
                            color="text.primary"
                            className="fw-bold"
                          >
                            {`${item.fieldOfStudy} at ${item.universityName}`}
                          </Typography>
                          <Typography variant="caption" color="text.secondary">
                            {`${moment(item.startDate).format("L")} - ${moment(
                              item.endDate
                            ).format("L")}`}
                          </Typography>
                        </Box>
                      </Stack>
                      {index + 1 !== length && <Divider />}
                    </Box>
                  )
                )}
              </CardContent>
            </Card>
          </Grid>

          {/* ================licenseCertification=============== */}
          <Grid item xs={12} mt={2}>
            <Card variant="outlined">
              <CardContent>
                {/* <Typography sx={{ fontSize: 14 }} color="text.secondary" mb={1}>
                  LICENSE & CERTIFICATION
                </Typography> */}
                {get(consultantMobileUIData, "licenseCertification", []).map(
                  (item, index, { length }) => (
                    <Box key={index}>
                      <Stack direction="row" align="center">
                        <Box component="span">
                          <Avatar
                            sx={{
                              backgroundColor: "grey.300",
                              color: "text.secondary",
                            }}
                          >
                            <CardMembershipIcon />
                          </Avatar>
                        </Box>
                        <Box sx={{ textAlign: "start", marginLeft: "10px" }}>
                          <Typography
                            variant="body2"
                            color="text.primary"
                            className="fw-bold"
                          >
                            {`${item.name} at ${item.issuingOrganization}`}
                          </Typography>
                          <Typography variant="caption" color="text.secondary">
                            {`${moment(item.issueDate).format("L")}`}
                          </Typography>
                        </Box>
                      </Stack>
                      {index + 1 !== length && <Divider />}
                    </Box>
                  )
                )}
              </CardContent>
            </Card>
          </Grid>

          {/* ================Service Plan=============== */}
          <Grid item xs={12} mt={2}>
            <Card variant="outlined">
              <CardContent>
                {/* <Typography sx={{ fontSize: 14 }} color="text.secondary" mb={1}>
                  SERVICE PLAN
                </Typography> */}
                {get(consultantMobileUIData, "servicePlan", []).map(
                  (item, index, { length }) => (
                    <Box key={index}>
                      <Stack direction="row" align="center">
                        <Box component="span">
                          <Avatar
                            sx={{
                              backgroundColor: "grey.300",
                              color: "text.secondary",
                            }}
                          >
                            {/* <MedicalInformationIcon /> */}
                            <BusinessCenterOutlinedIcon/>
                          </Avatar>
                        </Box>
                        <Box sx={{ textAlign: "start", marginLeft: "10px" }}>
                          <Typography
                            variant="body2"
                            color="text.primary"
                            className="fw-bold"
                          >
                            {item?.planName}
                          </Typography>
                          <Typography variant="caption" color="text.secondary">
                            {`${moment(item?.createDate).format("L")} | ${
                              item?.sessionName
                            }`}
                          </Typography>
                        </Box>
                      </Stack>
                      {index + 1 !== length && <Divider />}
                      {/* { index + 1 !== length && <hr />} */}
                    </Box>
                  )
                )}
              </CardContent>
            </Card>
          </Grid>

          {/* ================SOCIAL MEDIA=============== */}
          {/* <Grid item xs={12} mt={2}>
            <Card variant="outlined">
              <CardContent>
                <Typography sx={{ fontSize: 14 }} color="text.secondary" mb={1}>
                  SOCIAL MEDIA
                </Typography>
                <Stack direction="row" alignItems="center" spacing={1}>
                  {get(consultantMobileUIData, "socialMediaInfo", []).map(
                    (item, index) => (
                      <Box component="span">
                        <Avatar
                          sx={{
                            backgroundColor: "grey.300",
                            color: "text.secondary",
                          }}
                        >
                          <CMToolTip
                            icon={
                              <CopyToClipboard
                                text={item.chanelLink}
                                onCopy={() => setIsCopied(true)}
                              >
                                {getIconComponent(item.socialMediaChanel)}
                              </CopyToClipboard>
                            }
                            title="Copied!!!"
                          />
                        </Avatar>
                      </Box>
                    )
                  )}
                </Stack>
              </CardContent>
            </Card>
          </Grid> */}
        </Grid>
      </Box>
    </div>
  );
};

export default MobileViewProfile;
