import React, { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import CardContent from "@mui/material/CardContent";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import { get, map } from "lodash";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import {
  getConsultantData,
  getConsultantInfo,
  getConsultantMobileUIInfo,
} from "../../../store/slice/auth/signup/consultantInfoSlice";
import {
  profileSectionChildStatus,
  profileSectionStatus,
  profileStatusUpdate,
} from "../../../store/slice/dashboard/nutritionProfileSlice";
import { CategoryDesign } from "../../Icon";
import CMAccordion from "../../ui-components/accordion/CMAccordion";
import CMAccordionDetails from "../../ui-components/accordion/CMAccordionDetails";
import CMAccordionSummary from "../../ui-components/accordion/CMAccordionSummary";
import SwitchToggle from "../../ui-components/switchToggle/SwitchToggle";
import LicenseCertificationData from "./LicenseCertificationData";
import ProfessionalDomain from "./ProfessionalDomain";
import SocialLinksData from "./SocialLinksData";
import TopDetails from "./TopDetails";
import UniversityData from "./UniversityData";
import translate from "../../hooks/translate";
import "./mobileview.css";
import MobileViewProfile from "./MobileViewProfile";
import ServicePlanData from "./ServicePlanData";

const NutritionProfilePage = () => {
  const dispatch = useDispatch();
  const consultantData = useSelector(getConsultantData);
  const [expanded, setExpanded] = useState("panel0");
  const [secName, setSecName] = useState("");
  const loading = useSelector((state) => state.nutritionProfile.loading);
  const infoLoading = useSelector((state) => state.consultantInfo.loading);
  const meInfo = useSelector((state) => state.auth.meInfo);
  const consultantInfo = consultantData.data;
  const consultantMobileUIData = consultantData?.mobileUIData?.result;

  useEffect(() => {
    if (meInfo?._id) {
      dispatch(getConsultantMobileUIInfo(meInfo?._id));
    }
  }, []);

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  const handleStatus = async (event, key) => {
    setSecName(key);
    let data = {};
    switch (key) {
      case "firstNameStatus":
        data = { firstNameStatus: event.target.checked };
        break;
      case "lastNameStatus":
        data = { lastNameStatus: event.target.checked };
        break;
      case "emailStatus":
        data = { emailStatus: event.target.checked };
        break;
      case "mobileStatus":
        data = { mobileStatus: event.target.checked };
        break;
      case "bioStatus":
        data = { bioStatus: event.target.checked };
        break;
      case "profileUrlStatus":
        data = { profileUrlStatus: event.target.checked };
        break;
      default:
        break;
    }

    const res = await dispatch(profileStatusUpdate(data))
      .unwrap()
      .then((result) => {
        if (result.status) {
          dispatch(getConsultantInfo());
          toast.success(result.message);
        }
      })
      .catch((error) => {
        console.log("profile update error=======>", error);
      });
    return res;
  };

  const handleSectionStatus = async (event, sectionName) => {
    const payload = {
      sectionName: sectionName,
      status: event.target.checked,
    };
    setSecName(sectionName);
    const res = await dispatch(profileSectionStatus(payload))
      .unwrap()
      .then((result) => {
        if (result.status) {
          dispatch(getConsultantInfo());
          toast.success(result.message);
        }
      })
      .catch((error) => {
        console.log("profile update error=======>", error);
      });
    return res;
  };

  const handleSectionChildStatus = async (event, sectionName, id) => {
    event.stopPropagation();
    const payload = {
      _id: id,
      sectionName: sectionName,
      status: event.target.checked,
    };
    const res = await dispatch(profileSectionChildStatus(payload))
      .unwrap()
      .then((result) => {
        if (result.status) {
          dispatch(getConsultantInfo());
          toast.success(result.message);
        }
      })
      .catch((error) => {
        console.log("profile update error=======>", error);
      });
    return res;
  };

  const defaultProps = {
    consultantInfo,
    loading,
    infoLoading,
    handleSectionChildStatus,
  };

  const data = [
    {
      title: "consultant_education_university",
      sectionName: "educationUniversity",
      sectoinStatus: "universityStatus",
      component: <UniversityData {...defaultProps} />,
    },
    {
      title: "consultant_licence_or_certification",
      sectionName: "licenseCertification",
      sectoinStatus: "licenseCertificationStatus",
      component: <LicenseCertificationData {...defaultProps} />,
    },
    {
      title: "consultant_links",
      sectionName: "socialMediaInfo",
      sectoinStatus: "socialMediaInfoStatus",
      component: <SocialLinksData {...defaultProps} />,
    },
    {
      title: "consultant_service_plan",
      sectionName: "servicePlan",
      sectoinStatus: "servicePlanStatus",
      component: <ServicePlanData {...defaultProps} />,
    },
  ];

  return (
    <>
      <Grid container spacing={10} className="nutrition-profile-section">
        <Grid item xs={8}>
          <Paper variant="outlined" className="nutrition-bg">
            <CardContent>
              <TopDetails
                consultantInfo={consultantInfo}
                handleStatus={handleStatus}
                loading={loading}
                infoLoading={infoLoading}
                secName={secName}
              />
            </CardContent>
          </Paper>

          <ProfessionalDomain
            className="nutrition-special-design"
            consultantInfo={consultantInfo}
            handleSectionStatus={handleSectionStatus}
            {...defaultProps}
          />

          {map(data, (item, index) => (
            <Box py={1} key={index} className="nutrition-special-section">
              <CMAccordion
                expanded={expanded === `panel${index}`}
                onChange={handleChange(`panel${index}`)}
              >
                <CMAccordionSummary
                  expandIcon={<CategoryDesign sx={{ margin: 0 }} />}
                  reverse={true}
                >
                  <Typography variant="subtitle1">
                    {translate(item?.title)}
                  </Typography>
                  <SwitchToggle
                    // defaultChecked={get(consultantInfo, item.sectoinStatus, true)}
                    checked={get(consultantInfo, item.sectoinStatus, true)}
                    onChange={(e) => handleSectionStatus(e, item.sectionName)}
                    disabled={
                      secName === item.sectionName
                        ? loading || infoLoading
                        : false
                    }
                    inputProps={{
                      "aria-label": `${item.sectionName}-status`,
                      role: "switch",
                    }}
                  />
                </CMAccordionSummary>
                <CMAccordionDetails>
                  <Box>{item.component}</Box>
                </CMAccordionDetails>
              </CMAccordion>
            </Box>
          ))}
        </Grid>
        {/* ====== Mobile View ======= */}
        <Grid item xs={3}>
          <MobileViewProfile consultantMobileUIData={consultantMobileUIData} />
        </Grid>
      </Grid>
    </>
  );
};

export default NutritionProfilePage;
